.footer__container {
  background-color: #196bb1;
}

.footer__container .footer__inner {
  width: 1170px;
  grid-gap: 30px;
  grid-template-columns: repeat(4, 1fr);
  margin: 0 auto;
  padding: 75px 0;
  display: grid;
}

.footer__container .footer__inner a {
  display: block;
}

.footer__container .footer__inner a .footer__logo {
  width: 170px;
  display: block;
}

.footer__container .footer__inner .footer__section .footer__title {
  color: #2c2c2c;
  color: #fff;
  margin-bottom: 25px;
  font-size: 18px;
  font-weight: 700;
  line-height: 20px;
}

.footer__container .footer__inner .footer__section .footer__item {
  color: #2c2c2c;
  color: #ffffffbf;
  margin-bottom: 15px;
  font-size: 18px;
  font-weight: 400;
  line-height: 20px;
  transition: all .4s;
  display: block;
}

.footer__container .footer__inner .footer__section .footer__item:hover {
  color: #fff;
}

.footer__container .footer__inner .footer__section .footer__item:last-child {
  margin-bottom: 0;
}

.footer__container .footer__inner .footer__section .footer__item svg path {
  transition: all .4s;
}

.footer__container .footer__inner .footer__section .footer__item svg:hover path {
  fill-opacity: 1;
}

.footer__container .footer__inner .footer__section .footer__chats {
  align-items: center;
  gap: 15px;
  display: flex;
}

.footer__container .footer__inner .footer__section .footer__chats .footer__item {
  margin-bottom: 0;
}

